import { Popover } from '@headlessui/react'
import {useTranslation} from "react-i18next";

export default function Lang() {
  const {i18n} = useTranslation('common');   
 
  return (
    <>
    
    <Popover className="relative">
      <Popover.Button className="border-none bg-inherit cursor-pointer"><img src="./img/globe.svg" alt="" width="18px" /></Popover.Button>

      <Popover.Panel className="absolute z-10 pt-[13px] pls-[13px] bg-white w-[211px] rounded-[6px] ">
        <div>
          <a href="#" onClick={() => i18n.changeLanguage('ru')} className="text-[#282828] text-[16px] ml-[7px] mb-[17px] font-medium no-underline block bg-[#F5F5F5] py-[4px] px-[6px] rounded-[3px] w-[50%]">Русский</a>
          <a href="#" onClick={() => i18n.changeLanguage('kz')} className="text-[#282828] text-[16px] ml-[7px] mb-[17px] font-medium no-underline block">Қазақ тілі</a>
          <a href="#" onClick={() => i18n.changeLanguage('en')} className="text-[#282828] text-[16px] ml-[7px] mb-[17px] font-medium no-underline block">English</a>
          
        </div>

      </Popover.Panel>
    </Popover>
    </>
  )
}
