import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import { BrowserRouter } from 'react-router-dom';

import {I18nextProvider} from "react-i18next";
import i18next from "i18next";
import {Suspense} from 'react';

import CommonRu from "./lang/ru.json";
import CommonKz from "./lang/kz.json";
import CommonEn from "./lang/en.json";

i18next.init({
    interpolation: { escapeValue: false },  // React already does escaping
    lng: 'ru',                              // language to use
    resources: {
        en: {
            common: CommonEn               // 'common' is our custom namespace
        },
        kz: {
          common: CommonKz               // 'common' is our custom namespace
      },
        ru: {
            common: CommonRu
        },
    },
});

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <BrowserRouter>
    <Suspense fallback="loading">
    <I18nextProvider i18n={i18next}>
        <App />
        </I18nextProvider>
    </Suspense>
    </BrowserRouter>
  </React.StrictMode>
);

